import { NgModule, ModuleWithProviders } from '@angular/core';
import { RxIdleConfig } from './rx-idle.config';

@NgModule({
  declarations: [],
  imports: [
  ],
  exports: []
})
export class RxIdleModule { 
  static forRoot(config: RxIdleConfig): ModuleWithProviders {
    return {
      ngModule: RxIdleModule,
      providers: [
        {provide: RxIdleConfig, useValue: config}
      ]
    };
  }
}
